export interface ListNftProps {
    itemsRedeemed: number
}
const ListNft = (props: ListNftProps) => {
    return (
        <main className="container">
            <div className="row gy-5 gx-5">
                {nfts.map((e, key) => {
                    return (
                        <div className="col-6 col-lg-3 pointer hover-opacity-1" key={key} style={{opacity: props.itemsRedeemed<=key ? 1 : 0.2}}>
                            <img className="rounded w-100" src={e.image} alt={e.name} loading="lazy"/>
                            <div className="text-center mt-3">{e.name}</div>
                        </div>
                    );
                })}
            </div>
        </main>
    );
}
export default ListNft;